<template>
  <header class="head-nav">
      <el-row>
          <el-col :span="6" class="logo-container">
              <img src='../assets/logo.jpg' class="logo" alt="">
              <span class="title" >宠物医院在线后台管理系统</span>
          </el-col>
          <el-col :span="6" class="user">
              <div class="userinfo">
                  <img :src="user.avatar" class="avartar" alt="">
                  <div class="welcome">
                      <p class="name">欢迎</p>
                      <p class="name avartarname">{{ user.name }}</p>
                  </div>
                  <span class="username">
                      <el-dropdown trigger="click" @command="setDialogInfo">
                        <span class="el-dropdown-link">
                            <i class="el-icon-caret-bottom el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="info">个人信息</el-dropdown-item>
                            <el-dropdown-item command="logout">退出</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                  </span>
              </div>
          </el-col>
      </el-row>
  </header>
</template> 

<script>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  
    export default {
        name:"head-nav",
        computed:{
            user(){
                return this.$store.getters.user;
            }
        },
        methods:{
            setDialogInfo(cmdItem){
                // console.log(cmdItem);
                switch(cmdItem){
                    case "info" :
                        this.showInfoList();
                        break;
                    case "logout" :
                        this.logout();
                        break;
                }
            },
            showInfoList(){
                console.log("个人信息");
            },
            logout(){
                // console.log("退出");
                // 清除token
                localStorage.removeItem('eleToken');
                //设置vuex store
                this.$store.dispatch('clearCurrentState');
                //跳转
                this.$router.push("./login");
            }

        }
    };
</script>

<style scoped>
    .head-nav{
        width: 100%;
        height: 60px;
        min-width: 600px;
        padding: 5px;
        background: #423057;
        color: #fff;
        border-bottom: 1px solid #1f2d3d;
    }
    .logo-containter{
          line-height: 60px;
          min-width: 40px;
    }
    .logo{
        height: 50px;
        width: 50px;
        margin-right: 5px;
        vertical-align: middle;
        display: inline-block;

    }
    .titile{
        vertical-align: middle;
        font-size: 22px;
        letter-spacing: 3px;
    }
    .user{
        line-height: 60px;
        text-align: right;
        float: right;
        padding-right: 10px;
    }
    .avartar{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        vertical-align: middle;
        display: inline-block;
    }
    .welcome{
        display: inline-block;
        text-align: center;
        font-size: 14px;
    }
    .name{
        line-height: 20px;
        text-align: center;
        font-size: 14px;
    }
    .comename{
        font-size: 12px;
    }
    .avartarname{
        color: #409eff;
        font-weight: bolder;
    }
    .username{
        cursor:pointer;
        margin-right: 5px;

    }
    .el-dropdown{
        color:#fff;

    }
    
</style>